/* Начальные стили для элемента, который будет следовать за курсором, в нашем случае небольшой кружок */
.follow-cursor {
  opacity: 1;
  visibility: visible;
  border-radius: 0px;
  position: fixed; /* задаём абсолютное позиционирование, чтобы элемент не влиял на остальные элементы и его можно было бы позиционировать следом за курсором */
  position: fixed;
  left: 0;
  animation: mouseEffectNext 0.7s forwards linear;
  top: 0;
  z-index: 999; /* чтобы элемент был над остальными элементами */
  pointer-events: none; /* чтобы сквозь элемент можно было взаимодействовать с элементами, находящимися под ним */
  transform: scale(1);
  transition: width 0.64s, transform 12s, opacity 2s, height 0.64s, border-radius 0.64s, background 0.64s;
  height: 87px;
  width: 87px;
}
.follow-cursor:before {
  content: "";
  display: inline-block;
  width: 35px;
  height: 35px;
  position: absolute;
  background: rgba(63, 168, 227, 0.2);
  filter: blur(10px);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.follow-cursor:after {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  background: #3fa8e3;
  filter: blur(50px);
  width: 87px;
  height: 87px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

/* Активный класс для кружка при наведении на ссылку */
.hidden-mouse {
  visibility: visible;
  position: fixed; /* задаём абсолютное позиционирование, чтобы элемент не влиял на остальные элементы и его можно было бы позиционировать следом за курсором */
  left: 0;
  top: 0;
  animation: mouseEffect 0.7s forwards linear;
  height: 87px;
  width: 87px;
}
.hidden-mouse:before {
  content: "";
  display: inline-block;
  width: 35px;
  height: 35px;
  position: absolute;
  background: rgba(63, 168, 227, 0.2);
  filter: blur(10px);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.hidden-mouse:after {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  background: #3fa8e3;
  filter: blur(50px);
  width: 87px;
  height: 87px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

/* Скрываем декоративный элемент при ширине браузера меньше 992px */
@media (max-width: 991.98px) {
  .follow-cursor {
    display: none;
  }
}
@keyframes mouseEffect {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(0);
    opacity: 0;
  }
}
@keyframes mouseEffectNext {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.btn-up {
  display: none;
}
@media screen and (min-width: 1450px) {
  .btn-up {
    display: block;
    position: fixed;
    z-index: 100;
    right: 40px;
    bottom: 60px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 80px;
  }
  .btn-up:hover:before {
    opacity: 0;
    animation: none;
  }
  .btn-up:hover::after {
    opacity: 1;
    animation: none;
  }
  @keyframes arrow_vs_snowflake {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }
  .btn-up:after {
    content: "";
    transition: 0.3s;
    width: 80px;
    height: 80px;
    display: inline-block;
    border: 2px solid #fff;
    border-radius: 50%;
    background-image: url(../img/arrow-top.svg);
    background-repeat: no-repeat;
    background-size: 30%;
    background-position: 27px, 30px;
    animation: arrow_vs_snowflake 10s infinite linear;
    opacity: 0;
    z-index: 2;
  }
  .btn-up:before {
    content: "";
    transition: 0.3s;
    z-index: 1;
    display: inline-block;
    width: 80px;
    height: 80px;
    position: absolute;
    left: 0;
    top: 0;
    background: url(../img/svg/snowflake-arrow.svg) no-repeat;
    animation: snowflake 10s infinite linear;
  }
}

.btn-up_hide {
  display: none;
}

@keyframes arrow_vs_snowflake {
  0% {
    scale: 0;
    opacity: 0;
  }
  10% {
    scale: 0;
    opacity: 0;
  }
  40% {
    scale: 0;
    opacity: 0;
  }
  50% {
    scale: 1;
    opacity: 1;
  }
  97% {
    scale: 1;
    opacity: 1;
  }
  100% {
    scale: 0;
    opacity: 0;
  }
}
@keyframes snowflake {
  0% {
    scale: 0;
    transform: rotate(90deg);
  }
  7% {
    scale: 1;
    transform: rotate(0deg);
  }
  43% {
    scale: 1;
    transform: rotate(0deg);
  }
  50% {
    scale: 0;
    transform: rotate(90deg);
  }
  100% {
    scale: 0;
    transform: rotate(90deg);
  }
}
