  opacity: 1;
  transition: 300ms;
  cursor: pointer;
}
.dropdown__button span {
  color: #1f2123;
  padding: 15px 30px 15px 15px;
  background: #ffffff;
  border-radius: 10px;
  width: 100%;
  transition: 0.3s;
  display: block;
  width: 100%;
  position: relative;
}
.dropdown__button span:before {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
