.dropdown__button .arrow-triangle {
  position: absolute;
  right: 15px;
  top: calc(50% - 4px);
  transition: 0.3s;
  display: flex;
  align-items: flex-start;
  fill: #000000;
  transform: rotateX(-180deg);
}
